import React from 'react';


const colorMap = {
  緑: '#7fff7f',
  赤: '#ff7f7f',
  黄: '#ffff7f',
  青: '#7fbfff',
  紫: '#ff7fff',
}
const commonStyles = {
  width: 0,
  height: 0,
  borderStyle: 'solid',
  position: 'absolute',
};


export const GiftColor = ({ color }) => {


  const leftHalfStyles = {
    ...commonStyles,
    borderWidth: '15px 15px 15px 0',
    borderColor: `transparent ${colorMap[color[0]]} transparent transparent`,
    left: 0,
    top: 15,
  };

  const rightHalfStyles = {
    ...commonStyles,
    borderWidth: '15px 0 15px 15px',
    borderColor: `transparent transparent transparent ${colorMap[color[1]]}`,
    left: 14,
    top: 15,
  };

  const containerStyles = {
    width: '30px',
    height: '60px',
    position: 'relative',
    overflow: 'hidden',
  };




  return (
    <div style={containerStyles}>
      <div style={leftHalfStyles}></div>
      <div style={rightHalfStyles}></div>
    </div>
  );
};

export const GiftColorLR = (color, group) => {
  return (
    <div style={{ width: 12 }}>
      {group === 'giftColorL'
        ? <div
          style={{
            width: 5,
            height: 0,
            borderStyle: 'solid',
            position: 'absolute',
            borderWidth: '15px 15px 15px 0',
            borderColor: `transparent ${colorMap[color]} transparent transparent`,
            left: 0,
            top: 3,
          }}
        ></div>
        : <div
          style={{
            width: 5,
            height: 0,
            borderStyle: 'solid',
            position: 'absolute',
            borderWidth: '15px 0 15px 15px',
            borderColor: `transparent transparent transparent ${colorMap[color]}`,
            right: 0,
            top: 3,
          }}
        ></div>



      }
    </div>



  )


}

export const GiftColor1 = ({ color }) => {

  return (
    <div style={{
      position: 'relative',
      width: 16,
      height: 16
    }}>
      <div style={{
        width: 0,
        height: 0,
        borderStyle: 'solid',
        borderWidth: '0 8px 8px 8px',
        borderColor: `transparent transparent ${colorMap[color]} transparent`,
        position: 'absolute',
        top: 0,
        left: 0
      }}></div>
      <div style={{
        width: 0,
        height: 0,
        borderStyle: 'solid',
        borderWidth: '8px 8px 0 8px',
        borderColor: `${colorMap[color]} transparent transparent transparent`,
        position: 'absolute',
        bottom: 0,
        left: 0
      }}></div>
    </div>

  )
}

