import * as React from 'react';
import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';

import { TabContents } from './TabContents';


import { character } from '../constants/character';
import { skill, memoria, item } from '../constants/effects';
import { characterUtils, getStatusMax, skillUtils, getSkillStatusMax, alchemyUtils } from '../constants/constantsUtils';

characterUtils(character);
skillUtils(skill)
alchemyUtils(item)


const maxValues = {
  character: { ...getStatusMax(character), ...getSkillStatusMax(skill) },
  memoria: { ...getStatusMax(memoria) }
}






function CustomTabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box>
          {children}
        </Box>
      )}
    </div>
  );
}

CustomTabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

export default function TopTabs() {
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Box sx={{ width: '100%' }}>
      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={value} onChange={handleChange} aria-label='カテゴリー選択タブ'>
          <Tab label="キャラ" {...a11yProps(0)} />
          <Tab label="メモリア" {...a11yProps(1)} />
          <Tab label="アイテム" {...a11yProps(2)} />
          <Tab label="素材" {...a11yProps(3)} />
        </Tabs>
      </Box>
      <CustomTabPanel value={value} index={0}>
        <TabContents category={'character'} maxValues={maxValues.character} />
      </CustomTabPanel>
      <CustomTabPanel value={value} index={1}>
        <TabContents category={'memoria'} maxValues={maxValues.memoria} />
      </CustomTabPanel>
      <CustomTabPanel value={value} index={2}>
        <TabContents category={'item'} maxValues={''} />
      </CustomTabPanel>
      <CustomTabPanel value={value} index={3}>
        <TabContents category={'material'} maxValues={''} />
      </CustomTabPanel>
    </Box>
  );
}