import React, { useState, useEffect } from 'react';
import { Chip, Grid, List,ListSubheader, ListItem,ListItemButton,  Stack, Tooltip } from '@mui/material';
import { ListSubheaderCharacter, ListItemCharacter, ListSubheaderMemoria, ListItemMemoria, ListSubheaderItem, ListItemItem,ListItemMaterial } from './DataListSub'

const listItemStyle = (index) => ({
  border: '1px solid lightgray',
  borderTop: index > 0 ? 'none' : '1px solid lightgray',
  borderLeft: 'none',
  borderRight: 'none',
  width: '100%',
  height: 'auto',
  backgroundColor: '#596782',
  padding: 0,
});

const listSubheaderStyle = {
  backgroundColor: '#596782',
  height: 'auto',
  color: 'white',
  zIndex: 1,
  padding: 0,
  margin: 0,
  lineHeight: 1,
}

const listSubheaderComponents = {
  character: <ListSubheaderCharacter />,
  memoria: <ListSubheaderMemoria />,
  item: <ListSubheaderItem />,

};
const listItemComponents = {
  character: ListItemCharacter,
  memoria: ListItemMemoria,
  item: ListItemItem,
  material:ListItemMaterial,
};
export const DataList = ({ rows, category, maxValues, filterItems, sortItem, onSelect }) => {

  //  console.log(category,filterItems)

  return (
    <List
      sx={{ width: '100%' }}
      subheader={
        <ListSubheader sx={listSubheaderStyle}>
          {listSubheaderComponents[category]}
        </ListSubheader>
      }
    >
      {rows.map((dataEl, index) => {
        const ComponentToRender = listItemComponents[category];
        return (
          <ListItem key={dataEl.name} sx={listItemStyle(index)} >
            {/* <ListItemButton
              sx={{ padding: 0 }}
              onClick={() => onSelect(dataEl.name)}
            > */}
              <ComponentToRender
                category={category}
                dataEl={dataEl}
                maxValues={maxValues}
                filterItems={filterItems}
                sortItem={sortItem}
              />
            {/* </ListItemButton> */}
          </ListItem>
        )
      })}
    </List>
  )
}