export const TextBr = ({ text, name }) => {
  if (!text) return null;
  const regex = /\[red\](.*?)\[\/red\]/g;

  const parseLine = (line) => {
    let lastIndex = 0;
    const elements = [];
    let match;
    let elementIndex = 0; // Add this line to keep track of element index within the line

    while ((match = regex.exec(line)) !== null) {
      elements.push(<React.Fragment key={elementIndex++}>{line.substring(lastIndex, match.index)}</React.Fragment>);
      elements.push(<span key={elementIndex++} style={{ color: 'red' }}>{match[1]}</span>);
      lastIndex = regex.lastIndex;
    }

    elements.push(<React.Fragment key={elementIndex}>{line.substring(lastIndex)}</React.Fragment>);
    return elements;
  };

  return text.split(/(\n)/).map((line, index) => {
    if (line.match(/\n/)) {
      return <br key={index} />;
    } else {
      return <React.Fragment key={index}>{parseLine(line)}</React.Fragment>;
    }
  });
};


export function TextTlntTxt(props) {
  const { text } = props
  // console.log(text)
  return (
    text.map((texts, i) => {
      return (
        <div key={i}>
          {texts.map((text, i) => {
            return (
              text.includes(',') ?
                <span key={i} style={{ fontWeight: 'bold', color: 'blue' }}>{text}</span>
                :
                <span key={i}>{text}</span>
            );
          })}
        </div>
      );
    })
  );
}




export function TextCost({ cost }) {
  return (
    <div style={{ color: 'limegreen' }}>
      {cost === 1 ? '◆' :
        cost === 2 ? '◆◆' : ''}
    </div>
  )
}


export function TextCoolTime({ coolTime }) {
  return coolTime === 0 ? '-' : coolTime;
}

export function TextArea({ area, type }) {
  return type === 'passive' ? '-' :
    area === 0 ? '単' :
      (area * 10) % 10 === 1 ? '周' + Math.floor(area) :
        (area * 10) % 10 === 2 ? '直' + Math.floor(area) :
          area >= 10 ? '全' : area
}

export function TextRange({ range, type }) {
  return type === 'passive' ? '-' :
    range === 0 ? '自' : range
}

export function TextRv({ text }) {
  return (
    <span
      style={{
        color: 'white',
        backgroundColor: 'darkgray',
        borderRadius: 2,
        fontSize: 14,
        paddingLeft: 2,
        paddingRight: 2,
      }}
    >
      {text}
    </span>
  )
}

export function TextOrange(props) {
  const { text } = props
  return (
    <span style={{ color: 'white', backgroundColor: 'orange', borderRadius: 2 }} >
      &nbsp;{text}&nbsp;
    </span>
  )
}

export function TextGray(props) {
  const { text } = props
  return (
    <span style={{ color: 'white', backgroundColor: 'darkgray', borderRadius: 2 }} >
      &nbsp;{text}&nbsp;
    </span>
  )
}


