import * as React from 'react';
import { IconButton, ToggleButton, ToggleButtonGroup, Tooltip } from '@mui/material';
import { darken, lighten } from '@mui/system';


import { Img } from "./Img";

const text = {
  date: '実装日',
  damage0: '非クリティカル',
  damage1: '期待値',
  damage2: 'クリティカル',

}

const descriptionMap = {
  date: '「実装日」で並べ替え',
  damage0: '「非クリティカルダメージ」で並べ替え',
  damage1: '「ダメージ期待値(確率はクリティカル率のみ)」で並べ替え',
  damage2: '「クリティカルダメージ」で並べ替え',
}

const icon = {
  H: 'HP',
  A: 'アタック',
  D: 'ディフェンス',
  M: 'マジック',
  R: 'レジスト',
  T: 'テクニック',
}



export const SortButton = ({ props }) => {
  const { sortOrder, setSortOrder } = props
  const [isAscending, setIsAscending] = React.useState(sortOrder === 'asc' ? true : false);

  const handleToggle = () => {
    setIsAscending(!isAscending);
    const newSortOrder = isAscending ? "desc" : "asc"
    localStorage.setItem('sortOrder', newSortOrder);
    setSortOrder(newSortOrder);
  };

  return (
    <Tooltip title='昇順・降順' placement="top" arrow>
      <IconButton
        onClick={handleToggle}
        sx={{
          width: '36px',
          height: '36px',
          padding: '0px',
          backgroundColor: 'rgba(0, 0, 0, 0.08)',
          '&:hover': {
            backgroundColor: 'rgba(0, 0, 0, 0.08)'
          },
        }}
      >
        {isAscending ? '▲' : '▼'}
      </IconButton>
    </Tooltip >
  );
}

const containerStyle = {
  display: 'flex',
  flexWrap: 'wrap',
  // ...他のスタイルプロパティ
};
const toggleButtonStyle = {
  borderColor: 'silver',
  // fontWeight: 'bold',
  '&.Mui-selected': {
    color: '#596782',
    backgroundColor: '#EBE6DC',
    '&:hover': {
      backgroundColor: darken('#EBE6DC', 0.1),
    },
  },
  lineHeight: 1,
  padding: 0.5,
};
const textMap = {
  date:'実装日',
  damage0: 'スキル1ダメージ',
  damage1: 'スキル2ダメージ',
  damage2: 'スキル3ダメージ',
  break0: 'スキル1ブレイク',
  break1: 'スキル2ブレイク',
  break2: 'スキル3ブレイク',
  heal0: 'スキル1回復力',
  heal1: 'スキル2回復力',
  heal2: 'スキル3回復力',
  wait0: 'スキル1ウェイト',
  wait1: 'スキル2ウェイト',
  wait2: 'スキル3ウェイト',
}
const SplitTextWithBreak = ({ text }) => {
  const [firstPart, secondPart] = text.split(/(?<=\d)(?=[^\d])/); // スキル1ダメージ -> ["スキル1", "ダメージ"]
  return (
    <div>
      <div>{firstPart}</div>
      <div>{secondPart}</div>
    </div>
  );
};
const toggleButtonGroupStyle = (category) => ({
  pb: 0.5,
  height: category === 'character' ? 'auto' : 36,
  display: 'flex',
  flexWrap: 'wrap'
})
export const SortButtonGroup = (props) => {
  const { category, sortItem, setSortItem, sortItemList } = props.props

  const handleSortChange = (event, newSortItem) => {
    if (newSortItem) {
      setSortItem(newSortItem);
      // localStorage.setItem('sortItem', newSortItem);
    }
  };


  return (
    <div style={containerStyle}>
      <ToggleButtonGroup
        value={sortItem}
        exclusive
        onChange={handleSortChange}
        aria-label={'filterButton'}
        sx={toggleButtonGroupStyle(category)}
      >
        {sortItemList.map((name, i) => (
          <ToggleButton
            key={i}
            value={name}
            aria-label={name}
            size="small"
            sx={toggleButtonStyle}
          >
            <SplitTextWithBreak 
            text={textMap[name] ?? name} />
          </ToggleButton>
        ))}
      </ToggleButtonGroup>
    </div>
  )
}

