import React, { useState } from 'react';
import { Autocomplete, Chip, ToggleButton, ToggleButtonGroup, Grid, IconButton, Stack, TextField, Tooltip } from '@mui/material';
import { darken, lighten } from '@mui/system';
import HelpOutlineIcon from '@mui/icons-material/HelpOutline';
import StarIcon from '@mui/icons-material/Star'

import { memoria } from '../constants/effects';
import { gift } from '../constants/gift';
import { Img } from "./Img";
import { IsAnyMatch, IsAllMatch } from '../utils/commonUtils';
import { GiftColorLR, GiftColor1 } from './GiftColor';

const optionsOfMemoriaEffect = Object.values(memoria).map(memoriaEl => ({ name: memoriaEl.effectName }));
const optionsOfGift = Object.keys(gift).map(key => ({ name: key }));



const icon = ['アタッカー', 'ブレイカー', 'ディフェンダー', 'サポーター', '火', '氷', '風', '雷', '斬', '突', '打', '装備アイテム', '戦闘アイテム']
export const filterList = {
  character: {
    star: [3, 2, 1],
    role: ['アタッカー', 'ブレイカー', 'ディフェンダー', 'サポーター'],
    attribute: ['火', '氷', '雷', '風', '斬', '打', '突'],
    area: ['単', '全'],
    giftColorL: ['青', '紫', '黄', '赤', '緑'],
    giftColorR: ['青', '紫', '黄', '赤', '緑'],
    gift: [],
  },
  memoria: {
    rarity: ['SSR', 'SR', 'R'],
    effectName:[],
  },
  item: {
    rarity: ['SSR', 'SR', 'R'],
    kind: ['戦闘', '武器', '防具', '装飾品']
  },
  material: {
    target: ['装備アイテム', '戦闘アイテム'],
    rarity: ['SSR', 'SR', 'R'],
    giftColor: ['青', '紫', '黄', '赤', '緑','未確認'],
    gift: [],
  },


}
export const filterItemitialize = (category) => {
  return ({
    ...Object.fromEntries(Object.entries(filterList[category]).map(([key]) => [key, []])),
  })
}








const toggleButtonStyle = {
  borderColor: 'silver',
  '&.Mui-selected': {
    color: '#596782',
    backgroundColor: '#EBE6DC',
    '&:hover': {
      backgroundColor: darken('#EBE6DC', 0.1),
    },
  },
};

const buttonGroupStyle = {
  pb: 0.5,
  pr: 0.5,
  height: 36,
};

const giftColor = ['青', '紫', '黄', '赤', '緑']
const colorMap = {
  緑: '#7fff7f',
  赤: '#ff7f7f',
  黄: '#ffff7f',
  青: '#7fbfff',
  紫: '#ff7fff',
}

const StarIconGroup = ({ count, selected }) => {
  return (
    [...Array(Number(count))].map((_, starIndex) => (
      <StarIcon
        key={starIndex}
        style={{
          color: selected ? 'orange' : 'yellow',
          fontSize: '16px',
          marginRight: -3
        }}
      />
    ))
  );
};



export const FilterButtonGroup = ({ category, filterItems, setFilterItems, group }) => {
  const [items, setItems] = React.useState(filterItems[group]);

  const updateSelectedFilterItems = (event, newItems) => {
    setItems(newItems);
    const updatedFilterItems = {
      ...filterItems,
      [group]: [...newItems]
    }
    setFilterItems(updatedFilterItems)
    // localStorage.setItem('filterItems', JSON.stringify(updatedFilterItems));
  };

  const renderButton = (name, index) => {
    const isNumber = !isNaN(name);
    const isIcon = icon.includes(name);
    const isGiftColor = giftColor.includes(name);
    const selected = items.includes(name);

    const renderContent = (name, selected, icon, giftColor, group) => {
      if (isNumber) {
        return <StarIconGroup count={name} selected={selected} />;
      }

      if (isIcon) {
        return <Img
          name={name}
          square={30}
          borderRadius={group === 'role' ? 5 :group === 'target' ? 15: 0}
          rhombus={group === 'attribute' ? true : false}
        />;
      }

      if (isGiftColor) {
        if (category === 'material') {
          return <GiftColor1 color={name} />;
        } else {
          return GiftColorLR(name, group);
        }
      }

      return name;
    }

    return (
      <ToggleButton
        value={name}
        aria-label={name}
        key={index}
        size="small"
        sx={toggleButtonStyle}
        selected={selected}
      >
        {renderContent(name, selected, icon, giftColor, group)}
      </ToggleButton >
    );
  };


  return (
    <ToggleButtonGroup
      value={items}
      onChange={updateSelectedFilterItems}
      aria-label={'filterButton'}
      sx={buttonGroupStyle}
    >
      {filterList[category][group].map(renderButton)}
    </ToggleButtonGroup>
  )
}





const filterStrategies = {
  character: (dataEl, filterItems) => (
    (filterItems.star.length === 0 || filterItems.star.includes(dataEl.star)) &&
    (filterItems.role.length === 0 || filterItems.role.includes(dataEl.role)) &&
    (filterItems.attribute.length === 0 || IsAnyMatch(filterItems.attribute, Object.keys(dataEl.attribute))) &&
    (filterItems.area.length === 0 || IsAnyMatch(filterItems.area, Object.keys(dataEl.area))) &&
    (filterItems.giftColorL.length === 0 || filterItems.giftColorL.includes(dataEl.giftColor[0])) &&
    (filterItems.giftColorR.length === 0 || filterItems.giftColorR.includes(dataEl.giftColor[1])) &&
    (filterItems.gift.length === 0 || (IsAnyMatch(filterItems.gift, dataEl.gift)))
  ),
  memoria: (dataEl, filterItems) => (
    (filterItems.rarity.length === 0 || filterItems.rarity.includes(dataEl.rarity)) &&
    (filterItems.effectName.length === 0 || (IsAnyMatch(filterItems.effectName, dataEl.effectName)))
  ),
  item: (dataEl, filterItems) => (
    (filterItems.rarity.length === 0 || filterItems.rarity.includes(dataEl.rarity)) &&
    (filterItems.kind.length === 0 || filterItems.kind.includes(dataEl.kind))
  ),
  material: (dataEl, filterItems) => (
    (filterItems.target.length === 0 || filterItems.target.includes(dataEl.target)) &&
    (filterItems.rarity.length === 0 || filterItems.rarity.includes(dataEl.rarity)) &&
    (filterItems.giftColor.length === 0 || filterItems.giftColor.includes(dataEl.giftColor)) &&
    (filterItems.gift.length === 0 || (IsAnyMatch(filterItems.gift, dataEl.gift)))
  ),



};


export function IsFilterPassed({ category, dataEl, filterItems }) {
  const filterStrategy = filterStrategies[category];
  if (!filterStrategy) return true; // もしくはデフォルトの振る舞いやエラーを返す
  return filterStrategy(dataEl, filterItems);
}




//AbilityFilter


const chipStyle = {
  backgroundColor: 'orange',
  color: 'white',
  height: 18,
  padding: 0,
  margin: 0,
};

const outerGridStyle = {
  width:600,
  pr: 0.5,
  pt: 0.5,
  pb: 0.5,
}


const renderTagsFunction = (value, getTagProps) => (
  value.map((option, index) => (
    <Chip
      key={option.name}
      size="small"
      label={option.name}
      {...getTagProps({ index })}
      sx={chipStyle}
    />
  ))
);

export function AbilityFilter({ filterItems, setFilterItems, group }) {

  const [selectedAbilities, setSelectedAbilities] = React.useState(filterItems[group].map((item) => ({ 'name': item })));
  const [inputValue, setInputValue] = useState(''); // ここで入力フィールドの値を状態として保存します


  const updateSelectedAbilities = (newItemObj) => {
    setSelectedAbilities(newItemObj);
    const updatedFilterItems = {
      ...filterItems,
      [group]: newItemObj.map((el) => el.name)
    }
    setFilterItems(updatedFilterItems)
    localStorage.setItem('filterItems', JSON.stringify(updatedFilterItems));
  }

  return (
    <Grid container>
      <Grid item sx={outerGridStyle}>
        <Autocomplete
          multiple
          id="abilities-selection"
          options={ group === 'gift' ? optionsOfGift : optionsOfMemoriaEffect}
          getOptionLabel={(option) => option.name}
          value={selectedAbilities}
          isOptionEqualToValue={(option, value) => option.name === value.name}
          onChange={(event, newItemObj) => {
            updateSelectedAbilities(newItemObj)
          }}
          onInputChange={(event, newInputValue, reason) => {
            if (reason === "input") {
              setInputValue(newInputValue);
            }
          }}
          inputValue={inputValue} // ここで状態の値をAutocompleteに渡します    
          disableCloseOnSelect
          renderInput={(params) => (
            <TextField
              {...params}
              label={group === 'gift' ? 'ギフト' : '効果'}
              size='small'
              variant="outlined"
            />
          )}
          renderTags={renderTagsFunction}
        />
      </Grid>
    </Grid>
  );
}







