import React, { useState, useEffect } from 'react';
import { Chip, Grid, List, ListItem, ListSubheader, Stack, Tooltip } from '@mui/material';

import { character } from '../constants/character';
import { memoria, item } from '../constants/effects';
import { material } from '../constants/material';
import { DataList } from './DataList';

import { filterList, filterItemitialize, FilterButtonGroup, IsFilterPassed, AbilityFilter } from './Filter'
import { SortButton, SortButtonGroup } from './Sort'
import { AlchemySelect } from './AlchemySelect';

const sortItemList = {
  character: ['date', 'HP', '素早さ', '物攻', '物防', '魔攻', '魔防', 'damage0', 'break0', 'heal0', 'wait0', 'damage1', 'break1', 'heal1', 'wait1', 'damage2', 'break2', 'heal2', 'wait2'],
  memoria: ['date', 'HP', '素早さ', '物攻', '物防', '魔攻', '魔防'],
  item: ['date', 'HP', '素早さ', '物攻', '物防', '魔攻', '魔防'],
  material: ['date'],
}



const categoryMap = {
  character: character,
  memoria: memoria,
  item: item,
  material: material,
}

// console.log(item)


export const TabContents = ({ category, maxValues }) => {

  const [filterItems, setFilterItems] = useState(filterItemitialize(category));
  const [sortItem, setSortItem] = useState('date');
  const [sortOrder, setSortOrder] = useState('desc');

  const [selectedName, setSelectedName] = useState(null);
  const [visible, setVisible] = useState(true);
  const handleSelect = (dataName) => {
    setVisible(false);
    setTimeout(() => {
      setSelectedName(dataName);
      setVisible(true);
    }, 100);
  };
  const handleBack = () => {
    setVisible(false);
    setTimeout(() => {
      setSelectedName(null);
      setVisible(true);
    }, 100);
  };

  useEffect(() => {
    let newRows = [];
    Object.entries(categoryMap[category]).forEach(([dataName, dataEl]) => {
      if (IsFilterPassed({ category, dataEl, filterItems })) {
        newRows.push({
          ...dataEl,
          ...dataEl.status,
          name: dataName,
        })
      }
    })

    newRows.sort((a, b) => {
      if (a[sortItem] === undefined) return 1; // aを後ろに
      if (b[sortItem] === undefined) return -1; // bを後ろに

      const itemA = sortItem === 'date' ? new Date(a[sortItem]) : a[sortItem];
      const itemB = sortItem === 'date' ? new Date(b[sortItem]) : b[sortItem];
      if (sortOrder === 'asc') {
        if (itemA < itemB) return -1; // aがbより前にくる場合
        if (itemA > itemB) return 1;  // bがaより前にくる場合
      } else { // descの場合
        if (itemA < itemB) return 1;  // bがaより前にくる場合
        if (itemA > itemB) return -1; // aがbより前にくる場合
      }
      return 0;  // aとbが同じ場合
    });

    setRows(newRows);
  }, [filterItems, sortItem, sortOrder]);

  const [rows, setRows] = useState([]);

  return (
    <div style={{ padding: '4px' }}>
      {/* {!selectedName
        ? <div> */}
      <Grid container>
        <Grid item>
          <SortButton props={{ sortOrder, setSortOrder }} />
        </Grid>
        <Grid item>
          <SortButtonGroup props={{ category, sortItem, setSortItem, sortItemList: sortItemList[category] }} />
        </Grid>
      </Grid>


      <Grid container >
        {Object.keys(filterList[category]).map(group => (
          <Grid item key={group}>
            <FilterButtonGroup
              category={category}
              filterItems={filterItems}
              setFilterItems={setFilterItems}
              group={group}
            />
          </Grid>
        ))}
      </Grid>
      <Grid container >
        {(category === 'character' || category === 'material' ) &&
          <Grid item>
            <AbilityFilter filterItems={filterItems} setFilterItems={setFilterItems} group={'gift'} />
          </Grid>
        }
        {(category === 'memoria'  ) &&
          <Grid item>
            <AbilityFilter filterItems={filterItems} setFilterItems={setFilterItems} group={'effectName'} />
          </Grid>
        }
      </Grid>
      <DataList
        rows={rows}
        category={category}
        maxValues={maxValues}
        filterItems={filterItems}
        sortItem={sortItem}
      // onSelect={handleSelect}
      />
    </div>
  );
};
