import React, { useState, useEffect } from 'react';
import { Chip, Grid, List, ListItem, ListSubheader, Stack, Tooltip } from '@mui/material';
import { darken, lighten } from '@mui/system';
import StarIcon from '@mui/icons-material/Star'


import { skill } from '../constants/effects';
import { ability } from '../constants/ability';
import { gift } from '../constants/gift';

import { Img } from './Img'
import { TextBr } from './Text'

import { Gift } from './Gift'
import { GiftColor, GiftColor1 } from './GiftColor'




const chipStyle = {
  height: 18,
  fontSize: '12px',
  margin: '1px'
}


const width = {
  status: 420,
  skill: 600,
  ability: 200,
  gift: 220,
}
const statusList = ['HP', '素早さ', '物攻', '物防', '魔攻', '魔防']
const skillStatusMap = {
  damage: 'ダメージ',
  break: 'ブレイク',
  heal: '回復力',
  wait: 'ウェイト',
}

const Oval = ({ label }) => {
  const ovalStyle = {
    display: 'inline-flex',
    alignItems: 'center',
    justifyContent: 'center',
    width: '100%',
    height: '18px',
    fontSize: '12px',
    lineHeight: '18px',
    borderRadius: '9px',
    backgroundColor: lighten('#596782', 0.15),
    color: 'white',
    margin: '1px',
  };

  return (
    <div style={ovalStyle}>
      {label}
    </div>
  );
};

const statusStyle = {
  boxSizing: 'border-box', /* borderとpaddingを含めたサイズ指定を有効にする */
  width: 60,
  height: 20,
  position: 'relative',
  textAlign: 'right',
  border: '1px solid whitesmoke',
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'flex-end',
}



const statusChartStyle = (value, max, isSortItem) => (
  {
    position: 'absolute',
    top: 0,
    left: 0,
    height: '100%',
    width: value ? (value / max * 58) : 0,
    backgroundColor: isSortItem ? 'orange' : 'darkgray'
  }
)

const statusValueStyle = {
  width: 60,
  position: 'relative',
  right: '4px'
}


const skillStatus = ['damage', 'break', 'heal', 'wait']

const TooltipOfSkill = ({ skillName }) => {
  return (
    <div>
      <div>{skillName}</div>
      <div><TextBr text={skill[skillName].text} name={skillName} /></div>
    </div>
  )
}


const skillAreaStyle = (isFilterItemArea) => {
  return {
    width: 20,
    minWidth: 20,
    height: 20,
    textAlign: 'center',
    backgroundColor: isFilterItemArea ? 'orange' : 'transparent',  // 'default'を'transparent'に変更しました
  };
}
const splitSortItem = (text) => {
  const result = text.match(/([a-zA-Z]+)(\d+)/);
  if (result) {
    const [, status, index] = result;
    return { status, index };
  }
  return { status: '', index: '' };
};

const SkillStatusView = ({ skillName, skillStatus, sortItem, skillIndex, maxValues }) => {

  const { status, index } = splitSortItem(sortItem)





  return (
    <div style={{ display: 'flex' }}>
      {skillStatus.map(key => {

        let isSortItem = false
        if (key === status && skillIndex === Number(index)) {
          isSortItem = true
        }
        // console.log(key,status,skillIndex,index,isSortItem)

        return (
          <div key={key} style={{ ...statusStyle }}>
            <div style={statusChartStyle(skill[skillName][key], maxValues[key], isSortItem)}></div>
            <div style={statusValueStyle}>
              {
                skill[skillName][key]
                  ? skill[skillName][key].toLocaleString() + (key !== 'wait' ? '%' : '')
                  : '-'
              }
            </div>
          </div>
        )
      })}
    </div>
  );
};






export const ListSubheaderCharacter = () => {

  return (
    <Grid container >
      <Grid item sx={{ width: width.status, alignItems: 'center' }} >
        <Oval label='★はガチャ排出時、ステータスはガチャ詳細画面' />
        <div style={{ display: 'flex' }}>
          <div style={{ width: 60 }}></div>
          {statusList.map((key) => (
            <div key={key} style={{ width: 60 }}>
              <Oval label={key} />
            </div>
          ))}
        </div>
      </Grid>
      <Grid item sx={{ width: width.skill }} >
        <Oval label='スキル' />
        <div style={{ display: 'flex' }}>
          <div style={{ width: 40 }}></div>
          {Object.entries(skillStatusMap).map(([key, text]) => (
            <div key={key} style={{ width: 60 }}>
              <Oval label={text} />
            </div>
          ))}
        </div>
      </Grid>
      <Grid item sx={{ width: width.ability }} >
        <Oval label='アビリティ' />
      </Grid>
      <Grid item sx={{ width: width.gift }} >
        <Oval label='ギフト' />
      </Grid>
    </Grid>
  )
}

export const ListSubheaderMemoria = () => {
  return (
    <Grid container >
      <Grid item sx={{ width: 360, alignItems: 'center' }} >
        <Oval label='ステータスはガチャ詳細画面' />
        <div style={{ display: 'flex' }}>
          {statusList.map((key) => (
            <div key={key} style={{ width: 60 }}>
              <Oval label={key} />
            </div>
          ))}
        </div>
      </Grid>
    </Grid>
  )
}




export const ListItemCharacter = ({ dataEl, maxValues, filterItems, sortItem }) => {


  return (
    <Grid container sx={{ fontSize: '14px' }}>
      <Grid item sx={{ width: width.status }}>
        <div style={{ display: 'flex', height: 60, }}>
          <div style={{ width: 60 }}>
            <Img name={dataEl.name} square={60} />
          </div>
          <Stack >
            <div style={{ display: 'flex', alignItems: 'center' }}>
              <Img name={dataEl.role} square={40} />
              <div style={{ paddingLeft: 4, lineHeight: '1' }} >
                <div>
                  {[...Array(dataEl.star)].map((_, index) => (
                    <StarIcon key={index} style={{ color: 'yellow', fontSize: '16px', marginRight: -3 }} />
                  ))}
                </div>
                {dataEl.name.split('_')[0] + '【' + dataEl.name.split('_')[1] + '】'}
                {dataEl.name === 'レスナ_未来に夢を求めし者' && '　※数値は暫定'}
              </div>
            </div>
            <div style={{ display: 'flex' }}>
              {Object.entries(dataEl.status).map(([key, value]) => {
                const isSortItem = sortItem === key ? true : false
                return (
                  <div key={key} style={statusStyle}>
                    <div style={statusChartStyle(value, maxValues[key], isSortItem)}></div>
                    <div style={statusValueStyle}>{value.toLocaleString()}</div>
                  </div>
                )
              })}
            </div>
          </Stack>
        </div>
      </Grid>
      <Grid item sx={{ width: width.skill }}>
        <div style={{ alignItems: 'center', width: 280 }} >
          {dataEl.skill.map((skillName, skillIndex) => {
            const isFilterItemAttribute = filterItems.attribute.length > 0 && filterItems.attribute.includes(skill[skillName].attribute)
            const isFilterItemArea = filterItems.area.length > 0 && filterItems.area.includes(skill[skillName].area)
            return (
              <Tooltip
                key={skillName}
                placement="top-start" arrow
                title={<TooltipOfSkill skillName={skillName} />}
              >
                <div style={{ width: 280, display: 'flex' }}>
                  <div style={skillAreaStyle(isFilterItemAttribute)}>
                    <Img name={skill[skillName].attribute} square={20} rhombus={true} />
                  </div>
                  <div style={skillAreaStyle(isFilterItemArea)}>
                    {skill[skillName].area}
                  </div>
                  <SkillStatusView
                    skillName={skillName}
                    skillStatus={skillStatus}
                    sortItem={sortItem}
                    skillIndex={skillIndex}
                    maxValues={maxValues}
                  />
                  {skill[skillName].effects && skill[skillName].effects.map((effectEl) => {
                    return (
                      <Chip
                        key={effectEl.text}
                        label={effectEl.text}
                        size="small"
                        sx={chipStyle}
                      />
                    )
                  })}

                </div>
              </Tooltip>
            )
          })}
        </div>
      </Grid>

      {/* アビリティ */}
      <Grid item sx={{ width: width.ability }}>
        <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', width: 240, alignItems: 'flex-start' }}>
          {dataEl.ability.map(abilityName => {
            return (
              <Tooltip key={abilityName}
                placement="right"
                arrow
                title={<TextBr text={ability[abilityName].text} name={abilityName} />}
              >
                <Chip
                  label={abilityName}
                  size="small"
                  sx={{ height: 18, fontSize: '12px', margin: '1px' }}
                />
              </Tooltip>
            )

          })}
        </div>
      </Grid>

      {/* ギフト */}
      <Grid item sx={{ width: width.gift, display: 'flex' }}>
        <div style={{ width: 30, height: 60 }}>
          <GiftColor color={dataEl.giftColor} />
        </div>
        <Gift gifts={dataEl.gift} filterItems={filterItems} />

      </Grid>
    </Grid>

  )



}

export const ListItemMemoria = ({ dataEl, maxValues, filterItems, sortItem }) => {
  // console.log(dataEl)
  return (
    <Grid container sx={{ fontSize: '14px' }}>
      <Grid item>
        <div>
          {dataEl.rarity + ' ' + dataEl.name}{dataEl.provisional ? '　※数値は暫定' : ''}
        </div>
        <div style={{ display: 'flex' }}>
          {Object.entries(dataEl.status).map(([key, value]) => {
            const isSortItem = sortItem === key ? true : false
            return (
              <div key={key} style={statusStyle}>
                <div style={statusChartStyle(value, maxValues[key], isSortItem)}></div>
                <div style={statusValueStyle}>{value.toLocaleString() + '%'}</div>
              </div>
            )
          })}
        </div>
      </Grid>
      <Grid item>
        <div>
          <Chip
            label={dataEl.effectName}
            size="small"
            sx={chipStyle}
          />
        </div>
        <div style={{ fontSize: '12px', paddingLeft: 10 }}>
          {dataEl.text}
        </div>
      </Grid>
    </Grid>
  )
}

const statusKeys = ['HP', '素早さ', '物攻', '物防', '魔攻', '魔防']
const battleStatusKeys = ['damage', 'break', 'heal']

export const ListSubheaderItem = () => {
  return (
    <Grid container >
      <Grid item sx={{ width: 260, height: 20 }}>
      </Grid>
      <Grid item sx={{ width: 360, alignItems: 'center' }} >
        <div style={{ display: 'flex' }}>
          <div style={{ width: 180 }}>
            <Oval label='カテゴリ,属性,範囲,使用回数' />
          </div>
          <div style={{ width: 60 }}>
            <Oval label='ダメージ' />
          </div>
          <div style={{ width: 60 }}>
            <Oval label='ブレイク' />
          </div>
          <div style={{ width: 60 }}>
            <Oval label='回復力' />
          </div>

        </div>


        <div style={{ display: 'flex' }}>
          {statusList.map((key) => (
            <div key={key} style={{ width: 60 }}>
              <Oval label={key} />
            </div>
          ))}
        </div>
      </Grid>
    </Grid>
  )
}


export const ListItemItem = ({ dataEl, maxValues, filterItems, sortItem }) => {
  return (
    <Grid container sx={{ fontSize: '14px', alignItems: 'center' }}>
      <Grid item sx={{ width: 60, height: 20, display: 'flex', alignItems: 'center' }}>
        {dataEl.colors && dataEl.colors.map(color => (
          <GiftColor1 key={color} color={color} />
        ))}
      </Grid>

      <Grid item sx={{ width: 40, height: 20 }}>
        {dataEl.rarity}
      </Grid>
      <Grid item sx={{ width: 160, height: 20 }}>
        {dataEl.name}
      </Grid>
      <Grid item sx={{ width: 360, height: 20 }}>
        <div style={{ display: 'flex', width: 360 }}>
          {dataEl.kind !== '戦闘'
            ? statusKeys.map(key => {
              const isSortItem = sortItem === key ? true : false
              return (
                <div key={key} style={statusStyle}>
                  {/* <div style={statusChartStyle(dataEl[key] ?? 0, maxValues?.[key] ?? 150, isSortItem)}></div> */}
                  <div style={statusValueStyle}>{dataEl[key] ?? '-'}</div>
                </div>
              )
            })
            : <div style={{ display: 'flex' }}>
              <div style={{ width: 60, textAlign: 'center' }}>{dataEl.battleItemCategory}</div>
              <div style={{ width: 40, textAlign: 'center' }}>
                {dataEl.attribute &&
                  <Img name={dataEl.attribute} square={20} rhombus={true} />
                }
              </div>
              <div style={{ width: 40, textAlign: 'center' }}>{dataEl.area}</div>
              <div style={{ width: 40, textAlign: 'center' }}>{dataEl.usageCount + '回'}</div>
              {battleStatusKeys.map(key => {

                const isSortItem = sortItem === key ? true : false
                // console.log(key,status,skillIndex,index,isSortItem)

                return (
                  <div key={key} style={{ ...statusStyle }}>
                    {/* <div style={statusChartStyle(dataEl[key], maxValues[key] ?? 300, isSortItem)}></div> */}
                    <div style={statusValueStyle}>
                      {
                        dataEl[key]
                          ? dataEl[key].toLocaleString() + (dataEl.fixed ? '' : '%')
                          : '-'
                      }
                    </div>
                  </div>
                )
              })}
            </div>



          }
        </div>
      </Grid>
      <Grid item sx={{
        width: 600, height: 'auto', fontSize: '12px', paddingLeft: '4px', display: 'flex',
        alignItems: 'center'
      }}>
        <TextBr text={dataEl.text} name={dataEl.name} />
      </Grid>



    </Grid>
  )
}

export const ListItemMaterial = ({ dataEl, maxValues, filterItems, sortItem }) => {
  return (
    <Grid container>
      <Grid item sx={{ width: 40, height: 40, display: 'flex', alignItems: 'center' }}>
        <Img name={dataEl.target} square={30} borderRadius={15} />
      </Grid>
      <Grid item sx={{ width: 40, height: 40, display: 'flex', alignItems: 'center' }}>
        {dataEl.rarity}
      </Grid>
      <Grid item sx={{ width: 40, height: 40, display: 'flex', alignItems: 'center' }}>
        <GiftColor1 color={dataEl.giftColor} />
      </Grid>
      <Grid item sx={{ width: 120, fontSize: '14px', display: 'flex', alignItems: 'center' }}>
        {dataEl.name}
      </Grid>
      <Grid item sx={{ width: 240 }}>
        <Gift gifts={dataEl.gift} filterItems={filterItems} />
      </Grid>
    </Grid>
  )
}


