import { skill } from '../constants/effects';

const roles = ['アタッカー', 'ブレイカー', 'ディフェンダー', 'サポーター']
const attributeResistKeys = ['火', '氷', '風', '雷', '斬', '突', '打']

export const characterUtils = (character) => {

  for (const [charaName, charaEl] of Object.entries(character)) {

    //属性耐性
    for (const key of attributeResistKeys) {
      if (!charaEl.attributeResist[key]) {
        charaEl.attributeResist[key] = 0
      }
    }

    //スキル
    charaEl.attribute = {}
    charaEl.area = {}
    for (const [index, skillName] of charaEl.skill.entries()) {
      charaEl.attribute[skill[skillName].attribute] = true
      charaEl.area[skill[skillName].area] = true
      charaEl['damage' + index] = skill[skillName].damage ?? 0
      charaEl['break' + index] = skill[skillName].break ?? 0
      charaEl['heal' + index] = skill[skillName].heal ?? 0
      charaEl['wait' + index] = skill[skillName].wait ?? 0
    }



  }





}

export const getStatusMax = (data) => {

  const maxValues = {
    HP: 0,
    素早さ: 0,
    物攻: 0,
    物防: 0,
    魔攻: 0,
    魔防: 0,
  };

  for (const dataEl of Object.values(data)) {
    for (const key in dataEl.status) {
        if (maxValues[key] < dataEl.status[key]) {
          maxValues[key] = dataEl.status[key];
        }
    }
  }

  return (maxValues);  // 各ステータスの最高値を表示します



}

export const skillUtils = (skill) => {

  for (const [skillName, skillEl] of Object.entries(skill)) {

    if (!skillEl.text) {
      skillEl.text = '追加効果なし'
    }


  }





}


export const getSkillStatusMax = (skill) => {

  const maxSkillStatus = {};
  const skillArray = Object.values(skill);
  const keys = ['damage', 'break', 'heal', 'wait']

  //配列最初の値から比較していく
  const firstSkill = skillArray[0];
  for (const key of keys) {
    maxSkillStatus[key] = firstSkill[key] ?? 0;
  }
  for (let i = 1; i < skillArray.length; i++) {
    for (const key of keys) {
      if (!skillArray[i][key]) continue
      if (skillArray[i][key] > maxSkillStatus[key]) {
        maxSkillStatus[key] = skillArray[i][key];
      }
    }
  }

  return (maxSkillStatus);  // 各ステータスの最高値を表示します

}


export const alchemyUtils = (data) => {

  for (const [dataName, dataEl] of Object.entries(data)) {

    dataEl.name = dataName
    


  }





}

